<template>
  <div class="UserTag">
    <!-- list listFforme ListForOtherUser
    listFforPlate searchList FolAndFans FolAndFansForOther
    info read my otherUser GetArticleReward  Report

     -->
    <div class="middleTop" >
      <!-- 头像 -->
      <img :src='msgg.user_img' alt="" class="img" @click="toOtherUser($event)">
      <!-- 标签 -->
      <div class="tag">
        <!-- 昵称-->
        <div class="name">{{msgg.user_name}}</div>
        <!-- 金币-->
        <div class="goldDiv"><i class="iconfont gold">&#xe617;</i><span>{{msgg.user_gold}}</span></div>
        <!-- 等级 -->
        <div class="lvDiv iconfontDiv">LV {{msgg.user_lv| fLvComputed}}</div>
        <!-- 超级管理员-->
        <div class="user_super_admin iconfontDiv" v-if="msgg.user_super_admin == 1">超级管理员</div>
        <!-- 认证-->
        <div class="authenDiv iconfontDiv" v-if="msgg.user_authen == 1">认证</div>
        <!-- 超级认证-->
        <div class="superAuthenDiv iconfontDiv" v-if="msgg.user_authen == 2">超级认证</div>
        <!-- 美少女-->
        <div class="user_beauty iconfontDiv" v-if="msgg.user_beauty == 1">美少女</div>



      </div>
    </div>

  </div>
</template>

<script>
	/* 引入导航栏组件*/
	// import SignUp from "@/components/me/utils/SignUp"

  // import {createNamespacedHelpers} from 'vuex'
  // const navBar_Every=createNamespacedHelpers('navBar_Every')
  export default{
    name:"",
    /* 数据*/
    data(){
      return {
        userId : undefined
      }
    },
    props: {
        msgg: {
            type: Object, //指定传入的类型
            //type 也可以是一个自定义构造器函数，使用 instanceof 检测。
            default: {} //这样可以指定默认的值
        },
        toUserInfo: {
            type: Boolean, //指定传入的类型
            //type 也可以是一个自定义构造器函数，使用 instanceof 检测。
            default: false //这样可以指定默认的值
        }
    },
    /* 方法*/
    methods:{
      // ...nav.mapMutations({
      //       	signQueryHandler: 'signQueryHandler'
      //       }),
      toOtherUser(e){
        console.log(this.toUserInfo,'this.toUserInfo')
        // console.log(e==window.event,'window.event-----------')
        e = e || window.event;
        if(this.toUserInfo){
      　　if (e.stopPropagation) { //W3C阻止冒泡方法
              e.stopPropagation();
      　　} else {
      　　　　e.cancelBubble = true; //IE阻止冒泡方法
      　　 }
          // alert(this.userId)
          this.$router.push({
            path: '/otherUser',
            query : {
              userId : this.msgg.user_id
            }
          })
        }else{

        }
      }
    },
    /* 计算*/
    computed:{
     // ...nav.mapState({
     //      	code : state => state.code
     //      })
    },
    // 组件
    components : {
    	/* 这个组件名字不能和原生标签名重复 */
      // SignUp
    },
    /* 挂载阶段*/
    mounted() {
      // console.log(this.msgg,'msgg')
      // this.userId = this.msgg.id
    },
    // 监听
    watch:{
      // 'obj.a': {
      //     handler(newName, oldName) {
      //       console.log('obj.a changed');
      //     },
      //     immediate: true,
      //     // deep: true
      //   }
    },
    filters:{
      // fa : function(value){
      //   return Math.floor(parseInt(value)/100)
      // }
    }
  }
</script>

<style scoped lang="scss">
  .middleTop{
    // background-color:green;
    @include flex(row,space-between ,center)
    height: 100px;
    .img{
      @include wh(80px,80px)
      border-radius: 50%;
      object-fit: cover;
    }
    // 标签
    .tag{
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      flex-wrap:wrap ;
      // align-items: center;
      align-content : stretch;
      @include wh(580px,100px)
      // background-color:red;
      /* 所有di*/
      div{
        line-height: 36px;
        height: 36px;
        margin-right: 16px;
      }
      // 姓名div
      .name{
        font-size: 36px;
        font-weight: 600;
      }
      /* 所有图标div 除了goldDiv*/
      .iconfontDiv{
        border-radius: 10px;
        padding: 0 6px;
        font-size: 26px;
        color: white;
      }
      /* 图标所在div*/
      .user_super_admin{
        background-color: black;
      }
      .user_beauty{
        background-color: #f80;
      }
      .superAuthenDiv{
        background-color: #f80;
      }
      .authenDiv{
        background-color: #f80;
      }
      .lvDiv{
        background-color: #f80;
      }
      .goldDiv{
        font-size: 30px;
        color: $vantGreen;
        span{
          position: relative;
          top:-3px;
          left:3px;
        }
      }
      /*图标 */
      .lv{
        font-size: 30px;
      }
      .gold{
        font-size: 40px;
      }
    }
  }
</style>



